import React, { useState } from 'react'
import { Numbers, Operators } from './Data.js'
import CalPack from './CalPack.js'
import Button from '@mui/material/Button';
import './App.css';

const App = () => {
  const [flag, setFlag] = useState(false);
  const [value, setText] = useState('');
  const [answer, setAns] = useState('');
  const selectKey = (index, data) => {
    const variable = data.filter((val) => {
      return val.index === index;
    })

    if (flag === true && variable[0].index >= 12) {
      return;
    }

    if (variable[0].data !== '=') {
      let number = value + variable[0].data;
      setText(number);
      setAns(CalPack(number));
      if (variable[0].index >= 12) {
        setFlag(true);
      } else setFlag(false);
    }
    else {
      if (answer !== null) {
        setText(answer.toString());
        setAns(null);
      }
    }

  }
  return (

    <div className='cont-box d-flex justify-content-center align-items-center' style={{ height: '100vh', width: '100vw', backgroundColor: '#616161' }}>


      <div className="calcy" style={{ width: '30rem', backgroundColor: "white", border: '2px solid gray', borderRadius: '1rem' }}>
        <div className="cal-box">
          <section id='section'>
            <h1 style={{ textAlign: 'right', height: '5rem', padding: '1rem 3rem', color: 'gray',fontSize: '2rem' }}>{answer}</h1>
            <input className="px-3" type="text" name="teavt" id="text-number" value={value} autocomplete="off" style={{ width: '100%', border: 'none', fontSize: '3rem', outline: 'none', backgroundColor: 'white' }} disabled />
          </section>
        </div>
        <section className="cal-num row m-0 w-100" style={{ backgroundColor: '#263238', borderBottomLeftRadius: '1rem', borderBottomRightRadius: '1rem' }}>
          <aside style={{ width: "80%", backgroundColor: '#263238' }}>
            <div className="d-flex justify-content-evenly row">
              {Numbers.map((val, index) => {
                return (
                  <>
                    <div key={index} className=" col-lg-4 col-4 p-0 my-2">
                      <Button variant='text' className="w-100" onClick={() => { selectKey(val.index, Numbers) }} style={{ color: 'white', fontSize: '1.5rem' }}>{val.data}</Button>

                    </div>
                  </>
                )
              })}
            </div>
          </aside>
          <aside style={{ width: "20%", backgroundColor: '#00e676' }}>
            <div className=" row">
              {Operators.map((value, index) => {
                return (
                  < >
                    <div className="p-0 my-2">
                      <Button key={index} variant='text' className=" w-100" onClick={() => { selectKey(value.index, Operators) }} style={{ color: 'white', fontSize: '1.5rem' }}>{value.data}</Button>
                    </div>
                  </>

                );

              })}

            </div>
          </aside>
        </section>
        <div className="p-0 bottom-btn-del" >
          <Button className='bottom-btn-none' style={{ color: 'white', fontSize: '1.5rem', width: '100%', backgroundColor: '#263238', height: '100%' }} onClick={() => {
            setText(value.slice(0, -1));
            setAns(CalPack(value.slice(0, -1)));
          }}>DEL</Button>

        </div>

      </div>
    </div>

  )
}

export default App;
